// Import files

@import 'vars';


body {
    margin: 0;
    font-family: 'Rubik', sans-serif;
    font-size: 16px;
    color: $gray;

}

*,
*:after,
*:before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0;
}

// Import blocks

@import './blocks/container';
@import './blocks/header';
@import './blocks/nav';
@import './blocks/intro';
@import './blocks/btn';